import React from "react";
import Navbar from "../components/Navbar";
import Review from "../components/Review";
import "../styles/global.css";
import logoImage from "../images/logo.png"; // Changed from poodleImage to logoImage

export default function Home() {
  return (
    <div className="min-h-screen flex flex-col bg-pastel-lavender text-white overflow-y-auto">
      <Navbar />
      <header className="bg-pastel-lavender/30 py-12 sm:py-16 px-4 text-center border-2 border-white/20 rounded-b-lg">
        <div className="flex justify-center items-center flex-col">
          <h1
            className="text-3xl sm:text-4xl md:text-6xl font-cursive mb-2 text-center text-gray-900"
            style={{ textShadow: "1px 1px 3px rgba(0, 0, 0, 0.2)" }}
          >
            One<u>L</u>ove
          </h1>
          <h2 className="text-sm sm:text-base md:text-lg font-light text-gray-900">
            Grooming
          </h2>
          <p className="text-xs sm:text-sm md:text-base text-gray-700">Doorstep Care</p>
          <a
            href="/services"
            className="mt-4 inline-block bg-white text-pastel-lavender text-xs sm:text-sm md:text-base font-semibold py-2 px-4 rounded-full shadow-md hover:bg-gray-200 hover-glow transition"
          >
            <strong>Services</strong>
          </a>
        </div>
      </header>
      <section className="py-8 sm:py-12 px-4 max-w-3xl mx-auto text-pastel-lavender border-2 border-white/20 rounded-lg">
        <h2 className="text-2xl sm:text-3xl font-semibold mb-4 text-center text-gray-900">
          <u>Luxury Grooming</u>
        </h2>
        <h3 className="text-xl sm:text-2xl font-bold mb-2 text-center text-gray-900">
          <strong>Spa Experience</strong>
        </h3>
        <img
          src={logoImage} // Changed from poodleImage to logoImage
          alt="Logo"
          className="w-20 sm:w-24 mx-auto my-2 rounded-lg shadow-md"
        />
        {/* Space for Short Clip */}
        <div className="w-full h-24 sm:h-32 bg-white/10 border-2 border-dashed border-white my-4 rounded-lg flex items-center justify-center text-gray-600 text-sm">
          [Clip Placeholder]
        </div>
        <p className="text-sm sm:text-base mb-2 text-center text-gray-800">
          Pamper your pet with luxury care.
        </p>
        <ul className="text-sm sm:text-base list-disc list-inside mb-2 space-y-2 text-center mx-auto max-w-full sm:max-w-xl border-2 border-dashed border-white rounded-lg p-2 bg-white/10 text-gray-800">
          <li>
            <strong>
              <u>Hydra Bath</u>
            </strong>
            : Soft, shiny fur.
          </li>
          <li>
            <strong>
              <u>Nail Care</u>
            </strong>
            : Flawless paws.
          </li>
          <li>
            <strong>
              <u>Paw Elegance</u>
            </strong>
            : Royal comfort.
          </li>
          <li>
            <strong>
              <u>Ear Clean</u>
            </strong>
            : Clear ears.
          </li>
          <li>
            <strong>
              <u>Dental Care</u>
            </strong>
            : Bright smile.
          </li>
          <li>
            <strong>
              <u>Blowout</u>
            </strong>
            : Voluminous coat.
          </li>
          <li>
            <strong>
              <u>Fragrance</u>
            </strong>
            : Elegant scent.
          </li>
        </ul>
        <p className="text-sm sm:text-base text-center text-gray-800">
          Cage-free luxury with expert care.
        </p>
      </section>
      {/* Decorative Clip (Paw Print) */}
      <div className="text-center my-4">
        <svg
          className="w-8 sm:w-12 h-8 sm:h-12 mx-auto text-pastel-lavender"
          fill="currentColor"
          viewBox="0 0 24 24"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm-1 17.93c-3.95-.49-7-3.85-7-7.93 0-.62.08-1.21.21-1.79L9 14v1c0 1.1.9 2 2 2v1.93zm6.9-2.54c-.26-.81-1-1.39-1.9-1.39h-1v-3c0-.55-.45-1-1-1H8v-2h2c.55 0 1-.45 1-1V7h2c1.1 0 2-.9 2-2v-.41c2.93 1.19 5 4.06 5 7.41 0 2.08-.8 3.97-2.1 5.39z" />
        </svg>
      </div>
      <Review />
      <section className="py-8 sm:py-12 px-4 bg-pastel-lavender/30 text-pastel-lavender text-center border-2 border-white/20 rounded-lg">
        <h2 className="text-2xl sm:text-3xl font-semibold mb-4">
          <u>Book Now</u>
        </h2>
        <p className="text-sm sm:text-base italic">Coming Soon</p>
      </section>
    </div>
  );
}